import React from 'react';
import { Link } from 'react-router-dom';
import Info from '../common/Info';
import '../App.css';

class Lesly extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          show: false,
          name: 'Lesly',
          occupation: 'Software Engineer',
          timeSinceArrival: '15',
          country: 'Mexico',
          image: 'lesly_portrait.jpg'
        }

        setTimeout(() => {
            this.setState({ show: true });
        }, 1);
    }

    componentWillMount = () => {
        window.scrollTo(0, 0);
    }

    imageLoaded = () => {
        setTimeout(() => {
            this.setState({ show: false })
        }, 500);
    }

    render() {
        console.log(this.state);    
        return (
            <div className="participant">
            <div className="container">
               <ul className="row site-navigation">
            
                    <li className="col-12 col-sm-4 push-sm-4 logo">
                    <Link to={'/'}>
                        {'Faces of Daca'}
                    </Link>
                    </li>
                    <li className="col-6 col-sm-2 pull-sm-2 about-daca">
                    <a className="">About DACA</a>
                    </li>
                    <li className="col-6 col-sm-2 push-sm-2 about-site">
                    <a className="">How to help</a>
                    </li>
              </ul> 
                <div className="row">
                    <div className="portrait col-md-12">
                        <img src={`https://dwistynbcri9g.cloudfront.net/${this.state.image}`} alt={this.state.name}
                            onLoad={this.imageLoaded} 
                        /> 
                    </div>
                </div>
                <div className="row">
                    <div className="dacamentor-name col-md-12">
                        {'Lesly\'s Journey'}
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12 dacamentor-border-container">
                        <div className="dacamentor-border"></div>
                    </div>
                </div>
                <div className="row info">
                    <div className="info-container">
                        <Info
                            years={this.state.timeSinceArrival}
                            date={'Sept 12, 2017'}
                            occupation={this.state.occupation}
                            country={this.state.country}
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12 dacamentor-endborder-container">
                        <div className="dacamentor-endborder"></div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Tell me a bit about your early years and where you come from.'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'My brother and I are from Mexico and we lived there for five years. Both of my parents are from Oaxaca. My mother wanted us to move there to get out of the city and experience country life. For most of those two years we lived there, my dad had already been in the United States. We spent our time in Oaxaca without him and that was hard for my mom, my brother and myself. When we were seven and a half, my parents made the decision to move over here to the U.S. where a lot of my mothers family is currently located. I wasnt really aware of what was happening. We were pretty young. Nothing was really odd for us at first because our parents didnt really tell us.'}
                        </span>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'All I knew was that I was going to be able to see my dad again. And so, we got here in January of 2002. It wasnt too bad since we got put into bilingual and Spanish classes, and I guess it wasnt too much of a culture shock because of those accommodations.'}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Did your family know any English when you arrived to the United States?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'No, we didnt. We hadnt been exposed to it. And when we got here, it was definitely a big challenge for me since I didnt really have--I just had that feeling of not really belonging. And I think that fear of not understanding the language and not really learning it because, this might not be true for everybody, but at least for me, I really had that fear that the other children were going to laugh because I didnt pronounce something correctly. It was definitely a challenge at first. I want to say pretty much in elementary school it was like that. I did meet some great people. It wasnt until middle school where I really had to start learning and speaking [the language] so at that point I decided that I was going to stop speaking Spanish to really immerse myself to really get it. It worked out well and I started becoming fluent.'}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p>
                        <span className="dacamentor-response">
                            {'Middle school years were fine. In high school, I got really bored in basic English classes so I got my teachers to sign off for AP and Gate Honors classes. My parents really wanted us to go to college--it was one of the biggest things they encouraged us on. They wanted us to have the higher education that they couldnt get because they didnt get a chance to study. They had to work to support our family.'}
                        </span>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'I tried to have as many honors and AP classes, and did my best to make sure I got into the colleges where I could get scholarships. Being undocumented wasnt really that big of a deal for me. It never really came up. However, during my senior year, I hit a roadblock.'}
                        </span>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'When youre young, you dont have to apply for a drivers license and, for the most part, your parents took care of you. It wasnt until my senior year when I did college applications that I became aware of the impact of my status. I got accepted into a handful of colleges, but I couldnt apply for financial aid, so I wasnt able to go to the schools I wanted. I was accepted to Sacramento State, San Francisco, a private school in the LA area called University of La Verne, Cal State Monterey Bay, and some few others I cant remember at this moment. I really wanted to go to San Francisco or Los Angeles, but because they didnt offer enough financial aid I wasnt able to attend. At that point, it felt as if I really didnt have too many choices. After we graduated high school, my brother joined CS-in-3, a program where students are able to obtain their computer science degree in three years. I thought about applying, yet I didnt want to follow that path. Instead, I wanted to explore the subjects I wanted to study. I went to our local community college to continue my education. They had a lot of support and it wasnt as expensive. I studied there for a year--I wanted to study environmental science as a major. Most of the courses I took were chemistry classes, geology--things like that.'}
                        </span>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'After reflecting for a bit, however, I decided that computer science was going to be a good option for me because you can really do anything with it. Thats when I started the program and was lucky enough to get a scholarship to afford my undergrad.'}
                        </span>
                        </p>
                        <blockquote>
                            <p className="dacamentor-quote">
                                <span>
                                    {'"My parents really wanted us to go to college--it was one of the biggest things they encouraged us on. They wanted us to have the higher education that they couldnt get because they didnt get a chance to study. They had to work to support our family."'}
                                </span>
                            </p>
                        </blockquote>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Backtracking a bit, you mentioned when you first got here about feeling like you didnt belong. Has that changed now? Would you consider yourself an American?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'Definitely.  When I became more proficient and felt more comfortable with the language and the people, this place definitely felt like home. I started to get that sense of security and belonging. At some point when I was younger, it still felt very much like I wasnt part of what America represented. I was here for half of my life and half over there, but now, Ive been here for 15 years. It does feel like the United States is my home. I dont remember what its like to not be here.'}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Have you gone back to visit Mexico since you came?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'No, I havent. At some point I want to. Im not sure if I want to live there though, just because my roots are now grounded here. But I do really want to visit. I still have family there. Maybe Ill live there for a couple of months just to see what its like. But I definitely  consider this country to be my home and where I want to live for the rest of my life.'}
                        </span>
                        </p>
                        <blockquote>
                            <p className="dacamentor-quote">
                                <span>
                                    {'"When I became more proficient and felt more comfortable with the language and the people, this place definitely felt like home. I started to get that sense of security and belonging. At some point when I was younger, it still felt very much like I wasnt part of what America represented. I was here for half of my life and half over there, but now, Ive been here for 15 years. It does feel like the United States is my home. I dont remember what its like to not be here."'}
                                </span>
                            </p>
                        </blockquote>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'You mentioned you honed in on computer science for your undergrad. How was your college experience?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'I think my experience was great. I have met some wonderful people. I actually moved up to Seattle with a classmate where we both work and live together! In fact, I keep in contact with everybody. The people I met have been great and extremely supportive. In fact, they became a big part of my college experience. The past years were also a learning curve since I had no prior knowledge about CS before starting the program. The program definitely had its challenges, but the great sense of accomplishment where I was able to say that I got through these obstacles is the reason I am able to exercise my career today.'}
                        </span>
                        </p>
                        <blockquote>
                            <p className="dacamentor-quote">
                                <span>
                                    {'When youre young, you dont have to apply for a drivers license and, for the most part, your parents took care of you. It wasnt until my senior year when I did college applications that I became aware of the impact of my status. I got accepted into a handful of colleges, but I couldnt apply for financial aid, so I wasnt able to go to the schools I wanted.'}
                                </span>
                            </p>
                        </blockquote> 
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Overcoming obstacles definitely make one feel proud. So you moved out to Seattle and work at Liberty Mutual as a software engineer. What will you be working on?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'It depends on what field they put you in. During my time in college, I did a summer internship with Liberty Mutual. In my internship, my team was working closely with Microsoft and Amazon to do different projects and we used a lot of their resources. My team was creating visualization dashboards and applications that would benefit the work of employees within the company. As an insurance company, you wanna know what places are more likely to experience natural disasters and how it can affect both the customer and the company. So, for the first project I made an internal tool that visualized hurricane data, which lately has become extremely useful. Regarding the second project, I used the Python programming language. We have a lot of people that are under different sections (commercial, international insurance). This project helped the company look up the information of people and aggregate the tracking of different customers. Here, a lot of people are working on creating infrastructure and moving to the cloud. Overall, Im not sure what Im going to be working on next, but I enjoy doing web development and empowering coworkers with great software solutions to make their job easier. Therefore, I hope to work on something along those lines.'}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Although youre working full time now, do you plan on pursuing any higher education later on?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'I still have a strong interest in environmental science. Im hoping to get another degree in that field, hoping to find a company that intersects environmental science with computer science. If not, perhaps Ill even start my own company where the core values and tools we apply to our work could go a long way to benefit the environment. Its really important to look at this now since some people might not believe in climate change, but I believe these are the very things that we need to understand as humans living on this earth, to take care of our only home. '}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Long term, where do you see yourself in a few years?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'I always wanted to live in Seattle as far as I can remember. Ill be here for the next couple of years to see whether the place is a right fit. In the long term, I might see myself having a home in Seattle and maybe working at a different tech company. I definitely see myself still working - its something I really like doing and its fun. Perhaps, I could experience some different parts of the job to show what I can do and what I can contribute to other people. In my situation, women in tech remain underrepresented and I really want to inspire others to do something similar to what Ive done. Hopefully at that point herScript has grown as well.'}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'For those that dont know about herScript, can you expand a bit?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'herScript is an organization that the girls in my cohort started to encourage other women at Hartnell, CSUMB and even high schools to pursue an education in computer science, showing them what technology is all about. For a lot of us we didnt get that exposure until college happened. In some high schools, they offer some tech programs for their students. Meanwhile, in my school, tech programs were never talked about nor part of the schools curriculum. It was really never seen as an option. We wanted to make sure other girls had that exposure and support because in a lot of CS classes you dont see many girls--even now in the industry you dont see that many. We wanted to establish that support system for them to reach out to people like us who have already experienced it.'}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'Coming back full circle, do you plan on becoming a US citizen?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'I hope to one day become a citizen. I think that this has become my home and I see myself here for the rest of my life. To be able to contribute more to the place Ive called home for over 15 years, even vote for those that can represent and run the country well, that is what citizenship would mean for me. '}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'As you know, On Sept 5th, the Trump administration decided to rescind DACA thus leaving it up to Congress to find a solution. What was your initial reaction?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'It was pretty heartbreaking. With DACA, it was life changing. Without it, you dont have the same options as many citizens and residents do. It was only with DACA where I was able to get my license, a social security, and buy my first car at the beginning of this year. I was able to go to the east coast for an internship and work a bit during college. DACA gives you a lot of opportunity you wouldnt otherwise have. And to think that in two years when your permit ends, you might be out of work. You might have to go somewhere else; a place where, I assume, many have never really been to for quite awhile. It\'s heartbreaking.'}
                        </span>
                        </p>
                    </div>
                    <div className="col-md-12 dacamentor-interview">
                        <p className="dacamentor-question">
                            <b>
                                {'As Congress works on passing a resolution, is there anything youd like to say to them?'}
                            </b>
                        </p>
                        <p>
                        <span className="dacamentor-response">
                            {'As people, we are great contributors to the U.S, a place where weve rooted in, a place where weve gotten our education, and a place where we work at. I think were able to provide for not only the economy, but also our communities that we live in. I feel Ive made some impact during my time in California -- I was even able to go to middle schools, hoping to inspire students to consider the possibility of getting a higher education because its so important. I believe were invaluable in that sense.'}
                        </span>
                        </p>
                        <blockquote>
                            <p className="dacamentor-quote">
                                <span>
                                    {'"As people, we are great contributors to the U.S, a place where weve rooted in, a place where weve gotten our education, and a place where we work at."'}
                                </span>
                            </p>
                        </blockquote>
                    </div>
                </div>
            </div>
            </div>
        )
    }
}

export default Lesly;


