import React from "react";
import { Link } from "react-router-dom";
import MediaQuery from "react-responsive";
import Info from "../common/Info";
import "../App.css";

import ScrollLock from "react-scrolllock";
import MdClose from "react-icons/lib/md/close";
import MdMenu from "react-icons/lib/md/menu";
import Arrow from "react-icons/lib/fa/angle-down";
import Sticky from "react-stickynode";
import Facebook from "../img/facebook.svg";
import Instagram from "../img/instagram.svg";
import Twitter from "../img/twitter.svg";
import Billboard from "../img/billboard.jpg";
import { NavLink } from "react-router-dom";

class Xochitl extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      name: "Xochitl",
      occupation: "Behavioral Specialist",
      timeSinceArrival: "19",
      country: "Mexico",
      image: "xochitl_hero.jpg",
      interviewDate: "September 21, 2017",
      isMenuOpen: false,
      menuStyle: {
        height: "100%",
        left: 0,
        width: 0,
        top: 0,
        backgroundColor: "rgba(255, 255, 255, 0.95)",
        zIndex: 1,
        position: "fixed"
      },
      menuContent: {
        display: "none",
        position: "relative",
        top: "10%" /* 25% from the top */,
        width: "100%" /* 100% width */,
        textAlign: "center" /* Centered text/links */,
        marginTop:
          "30px" /* 30px top margin to avoid conflict with the close button on smaller screens */,
        zIndex: 2
      },
      portraitStyle: {
        backgroundImage: `url(https://dwistynbcri9g.cloudfront.net/xochitl_hero.jpg)`,
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundAttachment: "none",
        backgroundSize: "cover",
        height: 550,
        width: "90%",
        margin: "0 auto"
      }
    };

    this.mobileNavStyle = {
      display: "flex",
      justifyContent: "left",
      alignItems: "left",
      marginTop: 0
    };

    setTimeout(() => {
      this.setState({ show: true });
    }, 1);
  }

  imageLoaded = () => {
    setTimeout(() => {
      this.setState({ show: false });
    }, 500);
  };

  componentWillMount = () => {
    window.scrollTo(0, 0);
  };

  determinePath() {
    let path = "";
    switch (window.location.pathname) {
      case "/":
        "Stories";
        path = "Stories";
        break;
    }
    return path;
  }

  handleMenu = () => {
    this.setState({
      isMenuOpen: true,
      menuStyle: {
        ...this.state.menuStyle,
        width: "100%",
        opacity: 1
      },
      menuContent: {
        ...this.state.menuContent,
        display: "block"
      }
    });
  };

  closeMenu = () => {
    this.setState({
      isMenuOpen: false,
      menuStyle: {
        ...this.state.menuStyle,
        width: "0%",
        opacity: 0
      },
      menuListStyle: {
        display: "none"
      }
    });
  };

  render() {
    console.log(this.state);
    return (
      <div className="participant">
        <div className="flex-container">
          <Sticky enabled={true}>
            <div className="sidenav">
              <div className="title">
                <span>{"Faces Of Daca"}</span>
              </div>
              <div className="navigation">
                <ul>
                  <li>
                    <NavLink to="/" activeClassName="links">
                      Stories
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/statistics"
                      activeClassName="links"
                    />Statistics
                  </li>
                  <li>
                    <NavLink to="/help" activeClassName="links" />How To Help
                  </li>
                  <li>
                    <NavLink to="/help" activeClassName="links" />About
                  </li>
                  <div className="social">
                    <div>
                      <div>
                        <img id="facebook" src={Facebook} />
                      </div>
                      <div>
                        <img src={Instagram} />
                      </div>
                      <div>
                        <img src={Twitter} />
                      </div>
                    </div>
                  </div>
                </ul>
              </div>
            </div>
          </Sticky>
          <div className="main">
            <div className="mobile-menu" style={this.state.menuStyle}>
              <div className="overlay-content" style={this.state.menuContent}>
                <NavLink to="/" activeClassName="">
                  Stories
                </NavLink>
                <NavLink to="/statistics" activeClassName="">
                  Statistics
                </NavLink>
                <NavLink to="/help" activeClassName="">
                  How To Help
                </NavLink>
                <NavLink to="/help" activeClassName="">
                  About
                </NavLink>
              </div>
            </div>
            {this.state.isMenuOpen && <ScrollLock />}
            <div className="profile">
              <div className="">
                <div className="portrait" style={this.state.portraitStyle}>
                  <MediaQuery query="(max-width: 1224px)">
                    <div className="mobile-nav" style={this.mobileNavStyle}>
                      {this.state.isMenuOpen ? (
                        <MdClose
                          size={40}
                          onClick={this.closeMenu}
                          className="menu-close"
                          color={"black"}
                          style={{ margin: 10 }}
                        />
                      ) : (
                        <MdMenu
                          size={40}
                          onClick={this.handleMenu}
                          className="menu-icon"
                          color={"black"}
                          style={{ margin: 10 }}
                        />
                      )}
                    </div>
                  </MediaQuery>
                </div>
              </div>
              <div className="">
                <div className="dacamentor-name">
                  {`${this.state.name}'s Journey`}
                </div>
              </div>
              <div className="">
                <div className="dacamentor-border-container">
                  <div className="dacamentor-border" />
                </div>
              </div>
              <div className="info-container">
                <Info
                  years={this.state.timeSinceArrival}
                  date={this.state.interviewDate}
                  occupation={this.state.occupation}
                  country={this.state.country}
                />
              </div>
              <div className="">
                <div className="dacamentor-endborder-container">
                  <div className="dacamentor-endborder" />
                </div>
              </div>
              <div className="">
                <div className="dacamentor-interview">
                  <p>
                    <b>
                      {
                        "To start off, why dont you tell me about your early years and where you come from."
                      }
                    </b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "I was born in Mexico in Naucalpan de Juarez. I lived there for the first three years of my life and then when I was around 3, my parents brought me here in a car. We came here with a legitimate passport but it was only valid for two weeks. We came through the south border of Texas and resided in California because my dad has a brother that lived here."
                      }
                    </span>
                  </p>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>
                      {"Why did you and your family decide to come to the U.S?"}
                    </b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "We lived in an area in Mexico that wasnt very safe. It was very industrial and chemicals were released into the surrounding communities. It was affecting the health of the community; specifically, long-term lung damage. For their whole lives, my parents were exposed to a lot of crime and poverty. They knew the Unites States was the land of opportunity and they wanted a better life for us."
                      }
                    </span>
                  </p>
                  <blockquote>
                    <p className="dacamentor-quote">
                      <span>
                        {
                          '"I didnt know how different I was from a typical American child until middle school and high school where I felt more alienated because I started to think seriously about what I was going to do after school ended. There were opportunities I couldnt pursue because of my status--like my French class. They had a program to go experience other cultures and then I realized I couldnt go."'
                        }
                      </span>
                    </p>
                  </blockquote>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>
                      {
                        "Did you have a hard time fitting in when you first came?"
                      }
                    </b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "I went to an economically poor school for elementary--a dual-immersion school. It was full of children with a similar background like me so I didnt have a hard time fitting in. I wasnt ever made fun because of my English pronunciation. Maybe it was because I didnt go to school with my age group. I skipped a grade in elementary school and no one payed attention to mispronunciations. Additionally, I could have been oblvious to people acknowledging my accent. I didnt know how different I was from a typical American child until middle school and high school where I felt more alienated because I started to think seriously about what I was going to do after school ended. There were opportunities I couldnt pursue because of my status--like my French class. They had a program to go experience other cultures and then I realized I couldnt go."
                      }
                    </span>
                  </p>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>
                      {
                        "That happened to me too. I couldnt go to an all expenses paid trip to Japan in high school."
                      }
                    </b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "Sigh. I did a little explorer club in middle school where my mom payed a non-refundable fee of $500. I ended up not being able to go on the trip that year since they chose an out of the country destination. It sucks. At the time, I assumed that since it was a school trip, I would be able to go regardless of my illegal status. When I made the realization that I would not be able to participate I felt rejected. Eventually, the club didnt raise sufficient funds to continue on with the trip so they took a trip to San Francisco instead. I decided not to go regardless of my availability to join. I didnt want to be a part of a group where I had to announce to everyone of my illegal standing. I felt like an outcast. "
                      }
                    </span>
                  </p>
                  <blockquote>
                    <p className="dacamentor-quote">
                      <span>
                        {
                          '"I remember being in the car on my way home and my mom attempting to find other ways to reach home that wouldnt involve us being exposed to the deportation raid. I remember being so scared and hugging my little sister in an attempt to comfort her. "'
                        }
                      </span>
                    </p>
                  </blockquote>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "Ive always been aware about my status. There were issues in elementary school such as when they would do raids--you know, when ICE was more prevalent in 2005, 2006. My parents would be scared if there was a checkpoint or something like that. I was always exposed and I knew where I was standing but not in terms of being alienated. There was one occasion I remember specifically. I was about 7 or 8 years old. My mom was trying to learn English at a night class provided by a school. I remember being in the class with her when someone told everyone that there was a raid occurring, near the time we were ready to go home. I remember seeing fear everywhere. I remember being in the car on my way home and my mom attempting to find other ways to reach home that wouldnt involve us being exposed to the raid. I remember being so scared and hugging my little sister in an attempt to comfort her. I fell asleep after seeing extensive lights, I was so distressed I didnt want to be awake. Avoiding the police might not seem like a big deal, but as a 7/8 year old child, it is incredibly traumatizing to feel that my mom could be potentially taken away, or that I would be forced into living in a completely different country leaving my other siblings without a parent."
                      }
                    </span>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "In middle school and high school, its so crucial to fit in. Its seemingly the most important thing in the world at the time, and it really bothered me that I wasnt the same. I felt sad. I wouldnt want to speak Spanish in public. I felt embarrassed to do so. I wanted to fit in as much as I could. Looking back, it makes me feel so guilty. When my mom would ask me to translate something, I would get frustrated and think: Why cant you just learn English?!. It breaks my heart now when I think about those moments. I felt so much frustration that it was apparent that we were from Mexico. I was about 12 or 13 and I felt so much embarrassment over who I was. Thinking back, I feel so much more frustration knowing that those things bothered me. My parents gave up everything for me, absolutely everything. They missed out on the last years of their parents lives and they were not able to attend their parents funerals. Now I feel an overwhelming tremendous guilt knowing that I will never be able to repay them in the slightest for all of their sacrifices. "
                      }
                    </span>
                  </p>
                  <p>
                    <span className="dacamentor-response">{"[Pauses]"}</span>
                  </p>
                  <p>
                    <span className="dacamentor-response">{"Sorry."}</span>
                  </p>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>{"No, no, its okay."}</b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {"Have people cried in your interviews?"}
                    </span>
                  </p>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>
                      {
                        "Theres a good amount of interviews that became emotional. I told them it was okay to cry. Let yourself feel these emotions and take your time. Its not easy."
                      }
                    </b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {"Thank you. Give me a second."}
                    </span>
                  </p>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>{"Did your parents push you to do well in school?"}</b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "They never pushed me but I always felt like it was something I owed back to them. They definitely grounded me if I got bad grades but theyve always been really supportive. I know theres parents that create a lot of pressure for their children. But for my parents, they would support me and not berate me if I failed a test. The least I could do is get good grades for them because of the sacrifices they made. My drive to succeed wasnt because of pressure they created; I wanted to pay back what they have done for me. Theres a quote that states The pressure on first generation children of immigrants to prove that you are indeed proof of your parents sacrifice being worth it is something that many Americans will never understand."
                      }
                    </span>
                  </p>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>
                      {
                        "How was your high school experience? You mentioned this was when you felt alienated."
                      }
                    </b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "I really didnt like high school. I hated it. My freshman year I had the worst grades--I carried a 0.6 GPA. I dont know how thats possible but I had it. I never went to school and I would ditch every single day. In middle school, I had excellent grades. I had 3.8 GPA. Huge difference. I had so many things going on--not just my legal status. My mom asked me why I was doing so bad."
                      }
                    </span>
                  </p>
                  <p>
                    <span className="dacamentor-response">{"[Pauses]."}</span>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "And I remember telling her in tears, Whats the point? Im going to graduate and I cant do anything. I cant work. I cant even drive. Im stuck. Why am I even in school? She comforted me, saying that we would figure something out and to have hope of better things in 5 years. Her words didnt completely motivate me. I told myself I would get through high school, get my diploma and take it from there."
                      }
                    </span>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "It was really difficult to catch up since I had terrible grades for a whole year. I had to catch up on my math and English classes. At the time, my high school didnt offer saturday school or summer school. It was only available if you were a junior or a senior that needed to catch up on credits. The only thing I could do was make up credits at the local community college so thats what I did my junior and senior year of highschool. I was barely able to graduate."
                      }
                    </span>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "Isnt it crazy? As youre living through it, youre so embarrassed and full of shame. And when you come back to it, I can say that I lived through that, I went through that. Now Im here doing great things!"
                      }
                    </span>
                  </p>
                  <blockquote>
                    <p className="dacamentor-quote">
                      <span>
                        {
                          '"Theres a quote that states: The pressure on first generation children of immigrants to prove that you are indeed proof of your parents sacrifice being worth it is something that many Americans will never understand.""'
                        }
                      </span>
                    </p>
                  </blockquote>
                </div>
                <div className="dacamentor-interview">
                  <p>
                    <b>
                      {
                        "Yes! So you were able to climb back and finish high school--what were you thinking for next steps?"
                      }
                    </b>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "I didnt really know how college worked. Nobody in my family went to college. My friend Haley helped me through the process. She was like Obviously, youre going to college.. Our plan was to share books and take general education courses as we figured out the next steps. I didnt apply for DACA until 2013  because I didnt know about it. I didnt keep my ear to the ground and I thought if something came along, it would be obvious. My mom was the one that ended up telling me about DACA which is a little funny since shes not one to tune in to news and world events. "
                      }
                    </span>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "I hated not being able to work and support myself. I remember wanting to work when I turned 16 and I couldnt. It drove me crazy. I disliked relying on my parents for everything. I would feel bad asking them to even buy me deodorant. It was really hard for my parents to financially to provide for the four of us. As soon as I received my social security, I applied to a million places and landed my first job at Famous Footwear. Right away I went to get my drivers license. Thats the reason I never learned to drive until I was 20. Again, I thought Whats the point?. People would ask me why I didnt know how to drive. I wouldnt tell them why and I would just feel embarrassed."
                      }
                    </span>
                  </p>
                  <p>
                    <span className="dacamentor-response">
                      {
                        "I started off wanting to do psychiatric nursing. I studied that for a whole year and I realized I cant hear sad stories for my whole life--it would be too hard for me emotionally. I switched to neonatal nursing. I took nursing prerequisites for about a year until I saw I had a really hard time understanding biology. I switched back to psychology because I saw the overall theme of these careers: to help people and make a difference in their lives. This is my graduating semester; after this, Ill be graduating with my Associate degree in psychology. Ill be taking calculus and biology as prerequisites for UC Santa Cruz and UC Davis."
                      }
                    </span>
                  </p>
                </div>
              </div>
              <div className="dacamentor-interview">
                <p>
                  <b>
                    {"Sweet. Are these the only schools youre shooting for?"}
                  </b>
                </p>
                <p>
                  <span className="dacamentor-response">
                    {
                      "Yeah. Im leaning more towards Santa Cruz because  I have been told that it is the top school in the United States for research. Research Psychology is something that I am incredibly interested in. Moreover, UCSC provides so much support for undocumented students like me. I have been told that they offer free textbooks for dreamer students along with cheaper housing.  Im considering UC Davis is because my little sister goes there. Shes studying animal science and so far, she loves it there."
                    }
                  </span>
                </p>
              </div>
              <div className="dacamentor-interview">
                <p>
                  <b>
                    {
                      "Lets switch gears to recent events. On Sept 5th, the Trump administration announced its decision to rescind DACA. What was your initial reaction?"
                    }
                  </b>
                </p>
                <p>
                  <span className="dacamentor-response">
                    {
                      " I wasnt surprised when I heard of the termination of DACA. I was aware of the rumors going around. It just didnt make sense: Why? Why are they going to do this? I felt really angry and frustrated and taken advantaged of. Its hard to say this is our right because we didnt come to this country legally. Its not our natural born right. But  it is inhumane to do that to someone. I thought of how Im going to pay for college or how I would have to sustain myself.  That day, I heard about the protest that was going to happen in San Francisco and I drove up."
                    }
                  </span>
                </p>
              </div>
              <div className="dacamentor-interview">
                <p>
                  <b>{"How was it?"}</b>
                </p>
                <p>
                  <span className="dacamentor-response">
                    {
                      "It was amazing. It was just so heartwarming. It was hard being there because you hear everyone talking about it and you dont even want to accept that its happening. You want to be able to enjoy it, be part of it and enjoy everyones presence. It was hard because you didnt want the decision to be real. But it was nice to see so many people that supported us and not just people who benefitted from DACA but people in general. There were people screaming their lungs to defend me and people in my circumstances. To say that I was full of gratitude is a huge understatement. . This doesnt even affect you and youre here protesting for me. It was really wonderful. Everyone should go to one."
                    }
                  </span>
                </p>
              </div>
              <div className="dacamentor-interview">
                <p>
                  <b>
                    {
                      "Where do you see yourself in 5 years assuming nothing happens?"
                    }
                  </b>
                </p>
                <p>
                  <span className="dacamentor-response">
                    {
                      "Working and school. Still in school. I would probably be done with my B.A by then but I would want to go for my M.A, and possibly my doctorates. Im still contemplating  if it is something that I will be able to afford. Schools are such a business. If it wasnt for the money, I would definitely go for my doctorates. For right now, M.A. Baby steps first. Thankfully, we get more support now with things like AB-540 and it definitely helps out a lot to say the least. Unfortunately, that wasnt an opportunity for people like my older sister. My sister had outstanding grades during high school. She even had the choice of graduating in advance because she was so far ahead in her credits. When she graduated she was in a position where there was no financial help to assist her through college. She was unable to afford the classes therefore, never attended college."
                    }
                  </span>
                </p>
                <blockquote>
                  <p className="dacamentor-quote">
                    <span>
                      {
                        '"I want to feel like everyone else; to not be afraid that one day, I might be forced to live in a country with a high prevalence of violence--a country that I dont know. Theres nothing more that I want than to feel like I am accepted where I consider home."'
                      }
                    </span>
                  </p>
                </blockquote>
              </div>
              <div className="dacamentor-interview">
                <p>
                  <b>
                    {"Would you want to become a U.S citizen in the future?"}
                  </b>
                </p>
                <p>
                  <span className="dacamentor-response">
                    {
                      "Yes, I absolutely want to become a U.S citizen. I want to be able to live here. My family is here. I want to be able to come and go as I please and not have to worry if Im gonna be able to work next year. I want to be able to feel comfort in knowing that my freedom is no longer in jeopardy. I want to feel like everyone else; to not be afraid that one day, I might be forced to live in a country with a high prevalence of violence--a country that I dont know. Theres nothing more that I want than to feel like I am accepted where I consider home. If my illegal status remains the same, I will always be at risk of losing my three jobs, losing my ability to drive, never being able to travel, and most importantly, being forced out of my home while deprived of my family. I dont meet the requirements to renew. My DACA expires in Nov 2018. I dont know whats going to happen after that. I remain hopeful.  If nothing improves, I see myself still in school with the support of my parents.  However, that is something that I want to avoid at all costs. Theyre already paying for my little sisters tuition. I cant ask them to pay for mine as well."
                    }
                  </span>
                </p>
              </div>
              <div className="dacamentor-interview">
                <p>
                  <b>
                    {
                      "My last question: As Congress works towards a solution for us, what would you tell them if you had the chance?"
                    }
                  </b>
                </p>
                <p>
                  <span className="dacamentor-response">
                    {
                      "I hope that Congress can see us as more than monetary value. I hope that they recognize all of the sacrifices we have made to be where we are. I hope that they take our emotional distress into consideration. I hope that when they think of us and our parents, they consider how far they are willing to go for their children. I hope that they question how they would respond if they were put in the same situation my parents endured. I hope they question what they would want for their children.  Were people. Were human beings. I remain hopeful Congress will see that as they work on a solution in the next six months."
                    }
                  </span>
                </p>
                <blockquote>
                  <p className="dacamentor-quote">
                    <span>
                      {
                        '"I hope that when they think of us and our parents, they consider how far they are willing to go for their children. I hope that they question how they would respond if they were put in the same situation my parents endured."'
                      }
                    </span>
                  </p>
                </blockquote>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Xochitl;



